"use client"

import { getCalApi } from "@calcom/embed-react"
import { useEffect, useRef } from "react"
import { ArrowLeft } from "lucide-react"
import { Button } from "@/components/ui/button"
import Link from "next/link"

export function Cal() {
  const calContainerRef = useRef<HTMLDivElement>(null)
  const isInitialized = useRef(false)

  useEffect(() => {
    // Prevent multiple initializations
    if (isInitialized.current) return
    isInitialized.current = true

    const initCal = async () => {
      try {
        const cal = await getCalApi({ namespace: "15min" })
        cal("ui", {
          hideEventTypeDetails: false,
          layout: "month_view",
          styles: {
            branding: {
              brandColor: "#000000",
            },
          },
        })
      } catch (error) {
        console.error("Error initializing Cal.com:", error)
      }
    }

    initCal()
  }, [])

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <div className="sticky top-0 z-10 bg-background/80 backdrop-blur-sm border-b border-border">
        <div className="max-w-7xl mx-auto px-6 py-4 flex items-center justify-between">
          <Link href="/" className="flex items-center gap-2 text-muted-foreground hover:text-foreground transition-colors">
            <ArrowLeft className="w-5 h-5" />
            <span className="text-sm font-medium">Back to Home</span>
          </Link>
          <h1 className="text-2xl font-bold">Book a 15-min call</h1>
          <div className="w-20"></div>
        </div>
      </div>

      {/* Main Content */}
      <div className="max-w-7xl mx-auto px-6 py-12">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
          {/* Left Sidebar - Info */}
          <div className="lg:col-span-1 space-y-8">
            <div className="space-y-2">
              <h2 className="text-xl font-semibold">Lander Studio</h2>
              <p className="text-base text-muted-foreground">
                15 Min Meeting with Lander Studio
              </p>
            </div>

            <div className="space-y-4 text-sm text-muted-foreground">
              <p>This page is dedicated to scheduling client calls for lander.studio</p>
              <p>Thank you!</p>
            </div>

            <div className="pt-4 space-y-3">
              <div className="flex items-center gap-3">
                <div className="w-2 h-2 rounded-full bg-emerald-500 mt-1"></div>
                <span>15m Duration</span>
              </div>
              <div className="flex items-center gap-3">
                <div className="w-2 h-2 rounded-full bg-emerald-500 mt-1"></div>
                <span>Google Meet</span>
              </div>
              <div className="flex items-center gap-3">
                <div className="w-2 h-2 rounded-full bg-emerald-500 mt-1"></div>
                <span>Asia/Kolkata</span>
              </div>
            </div>

            <div className="pt-4">
              <p className="text-sm text-muted-foreground">
                Email us at{" "}
                <a
                  href="mailto:hello@lander.studio"
                  className="text-foreground font-medium hover:underline"
                >
                  hello@lander.studio
                </a>
              </p>
            </div>
          </div>

          {/* Right Content - Calendar */}
          <div className="lg:col-span-2">
            <div
              ref={calContainerRef}
              data-cal-namespace="15min"
              data-cal-link="nani-lm10/15min"
              data-cal-config='{"layout":"month_view"}'
              className="rounded-lg border border-border bg-card p-6 shadow-sm"
            />
          </div>
        </div>
      </div>
    </div>
  )
}